@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@405&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Josefin Sans', sans-serif;
  text-transform: uppercase;
}

body {
  background-color: black;
  color: white;
  width: 100%;
  max-height:1000px ;
}
.main_body{
  width: 100%;
  height: 100%;
  margin-top: 40px;
  margin-bottom: 10px;

}
.text_area{
  width: 100%;
  text-align: center;

}
i{
  color: red;

}
.live{
  font-size: 15px;
  height: 20px;
  -webkit-animation: live 3s linear infinite alternate;
          animation: live 3s linear infinite alternate;
}
@-webkit-keyframes live{
  from{
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  to{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}
@keyframes live{
  from{
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
  to{
    -webkit-transform: scale(1.4);
            transform: scale(1.4);
  }
}
h1{
  font-size: 30px;
  margin: 10px 0;
}
.main_div{
  margin-top: 3%;
  width: 85%;
  margin: auto;
  display: flex;
  padding: 20px;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;

}
.card{
  padding: 10px;
  height: 115px;
  width: 30%;
  text-align: center;
  margin: 20px 10px;
}
.card:nth-child(1){
  background: #1998fa;
}
.card:nth-child(2){
  background-color: #41a80d;
}
.card:nth-child(3){
  background-color: #b30b67;
}
.card:nth-child(4){
  background-color: #a82519;
}
.card:nth-child(5){
  background-color:  #b0ad02;
}
.card:nth-child(6){
  background-color: #009c8f;
}
h2{
  margin-bottom: 10px;
  padding: 0px;
  font-size: 15px;
}
span{
  font-size: 7px;
}

h3{
  font-size: 25px;
  text-align: center;
  margin-bottom: 10px;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.7),
            5px 5px 5px rgba(0, 0, 0, 0.6),
            10px 10px 5px rgba(0, 0, 0, 0.5),
            15px 15px 5px rgba(0, 0, 0, 0.4),
            20px 20px 5px rgba(0, 0, 0, 0.3),
            25px 25px 5px rgba(0, 0, 0, 0.2),
            30px 30px 5px rgba(0, 0, 0, 0.1);;

}

@media (max-width:768px ){
  .card{
    width: 40%;
  }
}


@media (max-width:425px ){
  .card{
    width: 80%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

